body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Simple reset and page style */
* {
    margin: 0;
    padding: 0;
}

html {
    background-color: #f3f3f3;
}

body {
    font-family: 'Open Sans';
}

img {
    cursor: pointer;
}

/* Wrapper */
.wrapper {
    display: flex;
    flex-flow: column wrap;
}

/* Nav */
nav {
    margin: 20px 0;
    text-align: center;
}

nav h1 {
    font-size: 30px;
}

nav .title {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 10px;
}

nav input {
    width: 90%;
    height: 60px;
    background-color: #f3f3f3;
    border: 0;
    border-bottom: 2px solid black;
    font-size: 30px;
}

nav ::-webkit-input-placeholder {
    color: darkgray;
    font-size: 30px;
}

/* Detail */
.detail {
    border-radius: 3px;
    margin: 10px;
    width: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-content: space-around;
}

.detail > li {
    border: 1px solid black;
    border-radius: 5px;
    margin:10px;
    padding-bottom: 20px;
    width: 100%;
    padding-left: 20px;
    color: black;
    font-size: 16px;
    list-style: none;
}

.detail h2 {
    text-align: center;
    color: #097cff;
    padding-bottom: 10px;
    font-size: 20px;
}

/* Ipads, Horizontal devices */
@media (min-width:480px) {
    .detail {
        justify-content: flex-start;
        flex-direction: row;
    }

    html {
        background-color: #b9fad7;
    }

    .detail .child {
        width: 40%;
    }
}

* {
    box-sizing: border-box;
}

input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
}

label {
    padding: 12px 12px 12px 0;
    display: inline-block;
}

.wrapper {
    margin: 0 10px;
}

button {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
    margin: 20px 40px;
}

button:hover {
    background-color: darkorange;
}

.col-25 {
    float: left;
    width: 25%;
    margin-top: 6px;
}
.col-75 {
    float:left;
    width: 75%;
    margin-top: 6px;
}

.row:after {
    content: '';
    display: table;
    clear: both;
}

.row-button {
    text-align: center;
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

@media screen and (max-width: 600px) {
    .col-25, .col-75, input[type=submit] {
        width: 100%;
        margin-top: 0;
    }
}
